<template>
  <transition name="fade-slow">
    <div v-if="!periods.length" key="loading" class="loading-container">
      <d-loading />
    </div>

    <div class="ct" v-else>
      <div v-for="(period, index) in periods" :key="index">
        <d-history-period-header :date="period[0].date" />
        <d-history-period
          v-for="item in period"
          :key="item.date.format()"
          :items="item.items"
          :date="item.date"
        >
        </d-history-period>
      </div>
      <div class="loading-icon-container">
        <d-loading text="loading" v-if="!isEnd" />
        <div v-else>- end -</div>
      </div>
      <div
        v-if="periods.length"
        v-observe-visibility="handleScolledToBottom"
      ></div>
    </div>
  </transition>
</template>

<script>
  import dHistoryPeriod from "@/components/history/dHistoryPeriod.vue";
  import dHistoryPeriodHeader from "@/components/history/dHistoryPeriodHeader.vue";
  import dcChartBase from "@/components/charts/dcChartBase.vue";

  import { mapGetters } from "vuex";

  export default {
    name: "History",
    components: {
      dcChartBase,
      dHistoryPeriod,
      dHistoryPeriodHeader,
    },

    data() {
      return {
        periods: [],
        lastDate: this.$moment().subtract(1, "week"),
        isEnd: false,
      };
    },
    computed: {
      ...mapGetters("stats", ["week"]),
      // ...mapGetters("stats/progress", { progress: "week" }),
    },

    methods: {
      async fetch() {
        this.periods = [this.week(this.$moment()), this.week(this.lastDate)];
      },

      handleScolledToBottom(isVisible) {
        if (this.isEnd) return;
        if (isVisible) {
          this.lastDate.subtract(1, "week");
          const items = this.week(this.lastDate);
          console.log("loading");
          if (items.length) {
            this.periods.push(items);
          } else {
            console.log("end");
            this.isEnd = true;
          }
        }
      },
    },

    mounted() {
      this.fetch();
    },
  };
</script>
<style lang="scss" scoped>
  .intro-chart {
    margin-bottom: 0.5rem;
  }
  .loading-icon-container {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
  }
  .ct {
    padding-top: 6rem;
    padding-bottom: 8rem;
  }
</style>
