<template>
  <div id="app">
    <d-header
      :menu="headerMenu"
      :title="headerTitle"
      :back="headerBack"
    ></d-header>
    <transition :name="transitionName" mode="out-in">
      <router-view />
    </transition>
    <transition name="fade">
      <d-sidebar v-show="isBottomMenuOpen" />
    </transition>
    <d-snackbar />
    <transition name="rotate-menu">
      <d-menu v-if="isMenuOpen" />
    </transition>
    <transition name="slide-bottom">
      <d-bottom-menu v-if="isBottomMenuOpen" />
    </transition>
  </div>
</template>

<script>
  import dBottomMenu from "@/components/menu/dBottomMenu.vue";
  import dSnackbar from "@/components/base/dSnackbar.vue";
  import { mapState, mapActions } from "vuex";

  export default {
    components: {
      dSnackbar,
      dBottomMenu,
    },
    metaInfo: {
      meta: [
        { charset: "utf-8" },
        {
          name: "viewport",
          content:
            "width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no",
        },
      ],
    },
    created() {
      this.initUserTheme();
      this.initStreaks();

      if (this.$workbox) {
        this.$workbox.addEventListener("waiting", () => {
          this.showUpdateUI = true;
        });
      }

      document.oncontextmenu = function(e) {
        e.preventDefault();
      };
    },
    mounted() {
      this.$router.push('/')
    },
    computed: {
      ...mapState({
        transitionName: (state) => state.transitionName,
        isMenuOpen: (state) => state.menu.open,
        isSidebarOpen: (state) => state.sidebar.open,
        isBottomMenuOpen: (state) => state.bottomMenu.open,
        headerTitle: (state) => state.headerTitle,
        headerMenu: (state) => state.headerMenu,
        headerBack: (state) => state.headerBack,
      }),
    },
    data() {
      return {
        elHeader: null,
        elView: null,
      };
    },
    methods: {
      ...mapActions({
        initUserTheme: "themes/init",
        initStreaks: "streaks/fetchPosts",
        initHistory: "history/init",
        initStats: "stats/init",
      }),
    },
  };
</script>

<style lang="scss">
  @import "./assets/styles/destyle.css";
  @import "@/assets/styles/scss/main.scss";
  @import "@/assets/styles/chartist/chartist.scss";
  @import "@/assets/styles/chartist/settings/_chartist-custom-settings.scss";
</style>
