<template>
  <div class="theme-menu-container">
    <div class="theme-menu">
      <div class="header-icon">
        <d-icon icon="palette" />
        <p>Themes</p>
      </div>
      <div class="color-list">
        <transition-group name="list">
          <d-color-label
            v-for="color in colors[selectedMode]"
            :key="color + selectedMode"
            :color="color"
          />
        </transition-group>
      </div>
    </div>
    <span class="back-btn" @click="closeThemeMenu">
      <d-icon id="icon" icon="chevron-right" @click.native="closeThemeMenu" />
    </span>
  </div>
</template>

<script>
  import { mapActions, mapState } from "vuex";
  import dColorLabel from "@/components/menu/dColorLabel.vue";

  export default {
    name: "d-theme-menu",
    data() {
      return {};
    },
    components: {
      dColorLabel,
    },
    computed: {
      ...mapState({
        colors: (state) => state.themes.colors,
        selectedMode: (state) => state.themes.selectedMode,
      }),
    },
    methods: {
      ...mapActions("themes", {
        closeThemeMenu: "close",
      }),
    },
  };
</script>

<style lang="scss" scoped>
  .theme-menu-container {
    color: var(--color-text-btn);
    background-color: var(--color-prime);
    border-radius: 20px;
    width: 8.8rem;
    height: 100%;
  }

  .theme-menu {
    position: relative;
    height: 100%;
    padding-top: 2.23rem;
    padding-left: 1.45rem;
    transform: rotate(-0.7deg);
  }

  .header-icon {
    position: absolute;
    top: 1.32rem;
    left: 1.2rem;
    display: flex;
    align-items: center;

    & p {
      margin-left: 0.65rem;
      margin-top: -0.5px;
    }
  }

  .color-list {
    margin-top: 0rem;
  }

  .back-btn {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 1.04rem 1.13rem;
