<template>
  <div class="item-container">
    <d-label class="header" xSmall>{{this.bottom ? "Bottom" : "Top" }}</d-label>

    <div :class="['item', {bottom}]" v-for="item in list" :key="item.id">
      <!-- <div class="progress-slide" :style="`transform:translateX(${-100 +  progress(item.id)}%;`"></div> -->
      <d-label xSmall>{{item.title}}</d-label>
      <dc-progress small perc :progress="progress(item.id)" />
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import dcProgress from "@/components/charts/dcProgress.vue";

export default {
  name: "dc-top-bottom-five",
  components: {
    dcProgress,
  },
  mounted() {
    const getter = this.bottom ? "bottom" : "top";
    this.list = this.$store.getters[`stats/progress/${getter}5`];
  },
  props: {
    bottom: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapGetters({ progress: "stats/item/progress" }),
  },
  data() {
    return {
      list: null,
    };
  },
};
</script>

<style lang="scss" scoped>
.item-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 0.3rem;
}

.header {
  margin-bottom: 0.3rem;
}

.item {
  overflow: hidden;
  position: relative;
  background-color: var(--color-bg-alpha);
  padding: 0.35rem 0.5rem;
  display: flex;
  justify-content: space-between;
  border-radius: 4px;
  color: var(--color-prime);
  // border: 1px solid var(--color-border-light);
  box-shadow: 5px 4px 9px -4px var(--color-shadow),
    -7px -6px 9px -4px var(--color-shadow);

  & .progress-slide {
    position: absolute;
    bottom: 0px;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: var(--color-prime-light);
    z-index: -1;

    // border-radius: 0 4px 4px 0;
  }
  &.bottom {
    color: var(--color-danger);
    & .progress-slide {
      background-color: var(--color-danger-light);
    }
  }
}
</style>